  @media (max-width: 768px) {
    nav {
      height: auto;
      margin-top: 50px;
      width: 100%; } }
  nav .menu {
    list-style-type: none;
    display: flex;
    padding: 0;
    margin: 0; }
    nav .menu.active {
      display: block; }
    @media (max-width: 768px) {
      nav .menu {
        flex-direction: column;
        width: 100%;
        display: none; } }
  nav .menu__element {
    margin-right: 35px;
