    nav .menu__element:last-child {
      margin-right: 0; }
    @media (max-width: 1090px) {
      nav .menu__element {
        margin-right: 20px; } }
    @media (max-width: 768px) {
      nav .menu__element {
        margin: 15px 0; } }
  nav .menu__link {
    font-family: "Raleway";
    text-decoration: none;
    color: #a4a4a4;
    transition: color;
    transition-duration: 500ms;
    position: relative; }
    nav .menu__link:hover {
      color: #ffffff;
      transition: color;
      transition-duration: 500ms; }
      nav .menu__link:hover:before {
        opacity: 1;
        transition: opacity;
        transition-duration: 500ms; }
      nav .menu__link:hover:after {
        opacity: 1; }
    nav .menu__link:before {
      position: absolute;
      content: "";
      width: 27px;
      height: 3px;
      background-color: #ff0036;
      left: 0;
      right: 0;
      top: -27px;
      margin: auto;
      opacity: 0;
      transition: opacity;
      transition-duration: 500ms;
      pointer-events: none; }
      @media (max-width: 768px) {
        nav .menu__link:before {
          display: none; } }
    nav .menu__link:after {
      position: absolute;
      content: "";
      width: 27px;
      height: 3px;
      background-color: #ff0036;
      left: 0;
      right: 0;
      bottom: -24px;
      margin: auto;
      opacity: 0;
      transition: opacity;
      transition-duration: 500ms;
      pointer-events: none; }
      @media (max-width: 768px) {
        nav .menu__link:after {
          display: none; } }
    @media (max-width: 768px) {
      nav .menu__link {
        width: 100%;
        display: block; } }

.menu__burger {
  width: 25px;
  height: 30px;
  display: none;
  position: relative;
  cursor: pointer; }
  .menu__burger:before {
    content: "";
    width: 25px;
    height: 3px;
    background-color: #fff;
    position: absolute;
    top: 0; }
  .menu__burger:after {
    content: "";
    width: 25px;
    height: 3px;
    background-color: #ffffff;
    position: absolute;
    bottom: 0; }
  .menu__burger .menu__burgerSpan {
    width: 25px;
    height: 3px;
    background-color: #ffffff;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto; }
  @media (max-width: 768px) {
    .menu__burger {
      display: block; } }

